import React from 'react'
import 'babel-polyfill' 
import ReactDOM from 'react-dom'
import { Form, Icon, Input, Button, Checkbox, message } from 'antd'
import { signInApi } from './../api/sign-in'
// import { signInApi } from './../api/sign-in'

const FormItem = Form.Item
//  model{name:,gender:,password:''} -> 
const SignInForm = Form.create()(React.createClass({
    async handleSubmit (e) {
        console.log(e)
        e.preventDefault()
        var values = await this.getFormValues()
        if (values) {
            //  console.log(values)
             let result = await signInApi(values)
            //  
             if (result.login == 'success') {
                 message.success('')
             } else {
                 message.error(result.message);
             }
        } else {
            // var values = {name: 'admin001', password: '123456' }
            // let result = await signInApi(values)
            message.error(',')
        }
    },
    getFormValues () {
        //  
        // username -> 
        return new Promise((resolve, reject) => {
            // 
            this.props.form.validateFields((err, values) => {
                if (!err) {
                    resolve(values)
                } else {
                    reject (false)
                } 
            })
        })
    },
    render () {
        const { getFieldDecorator } = this.props.form
        // console.log(this.props.form)
        return (
            <div style={{width: "280px", margin: "0 auto"}}>
                <Form onSubmit={this.handleSubmit} className="login-form">
                    <FormItem>
                        {getFieldDecorator('userName', {
                            rules: [{required: true, 
                            message: '!'}]
                        })(
                            <Input addonBefore={<Icon type="user" />} 
                            placeholder="!"/>
                        )}
                    </FormItem>
                    <FormItem>
                        {getFieldDecorator('password', {
                            rules: [{required: true, 
                            message: '!'}]
                        })(
                            <Input addonBefore={<Icon type="lock" />} 
                            type="password" placeholder="!"/>
                        )}
                    </FormItem>
                    <FormItem>
                        <a className="login-form-forgot"></a>
                        <Button 
                        type="primary"
                        htmlType="submit"
                        className="login-form-button">
                        </Button>
                    </FormItem>
                </Form>
            </div>
        )
    }
}))
export default SignInForm



